import React, { useState, useContext, useEffect } from "react";
import { ThemeContext } from "./ThemeContext";

export default function AddNoteForm(){
  const { colors } = useContext(ThemeContext);
  const [caseId, setCaseId] = useState("");
  const [deadline, setDeadline] = useState("");
  const [result, setResult] = useState("");
  const [isHovered, setIsHovered] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

    // CSS for the spinning loader
  const spinnerStyle = {
    border: `3px solid ${colors.spinnerBorder}`,
    borderTop: `3px solid ${colors.spinnerBorderTop}`,
    borderRadius: '50%',
    width: '9px',
    height: '9px',
    animation: 'spin 1s linear infinite',
    display: 'inline-block',
    marginRight: '8px',
  };

  // Keyframes for the spinner animation (applied via a style tag conceptually)
  const keyframes = `
    @keyframes spin {
      0% { transform: rotate(0deg); }
      100% { transform: rotate(360deg); }
    }
  `;

  const getToday = () => {
    const today = new Date();
    const year = today.getFullYear();
    const month = String(today.getMonth() + 1).padStart(2, '0');
    const day = String(today.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  };

    useEffect(() => {
        setDeadline(getToday());
        const handler = (eventArgs) => {
            setCaseId("");
            setResult("");
            getCaseID();
        };

        getCaseID();
        const itemType = Office.context.mailbox.item.itemType;
        const isReadMode = itemType === Office.MailboxEnums.ItemType.Message

        if(isReadMode){
          Office.context.mailbox.addHandlerAsync(
              Office.EventType.ItemChanged,
              handler
          );
        }

        return () => {

          if(isReadMode){
              Office.context.mailbox.removeHandlerAsync(
                  Office.EventType.ItemChanged,
                  handler
              );
          }
        }
    }, []);

    const getCaseID = async () => {
      setResult("");
        try {
            const currentItem = Office.context.mailbox.item;
            if (!currentItem) {
                return;
            }
            const messageId = currentItem.itemId;
            const emailAddress = Office.context.mailbox.userProfile.emailAddress;
            const conversationId = currentItem.conversationId;
            const internetMessageId = currentItem.internetMessageId;
            if (!messageId && !conversationId && !internetMessageId) {
              console.log("No valid identifiers found for the email.");
                return;
            }
            const payload = {
                MessageID: messageId,
                Email: emailAddress,
                ConversationID: conversationId,
                InternetMessageID: internetMessageId
            };
            const response = await fetch("https://outlook-rbs.it-bed.workers.dev/findCaseID", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(payload)
            });
            if (response.ok) {
                const data = await response.json();
                const text = JSON.parse(data.text)
                setCaseId(text.CaseID? text.CaseID : "");
                if (text.Users && Array.isArray(text.Users) && text.Users.length > 0) {

                  setResult(`A note has already been created for this email by : ${text.Users.join(", ")}`);
                } else {
                  setResult("");
                }
            } else {
                const text = await response.text();
                setResult("❌ Error: " + text);
            }
        } catch (error) {
            setResult("❌ Error: " + error.message);
        }
    };
    const addNote = async () => {   
        try {
          if (isLoading) return; // Prevent multiple submissions

            if (!caseId || !deadline) {
                setResult('❌ Please fill in all fields.');
                return;
            }
            const currentItem = Office.context.mailbox.item;
            if (!currentItem) {
                setResult('❌ No email item found.');
                return;
            }
            
            const messageId = currentItem.itemId;
            const emailAddress = Office.context.mailbox.userProfile.emailAddress;
            if (!messageId) {
                setResult('❌ Cannot add note for draft email.');
                return;
            }
            setResult("");
            setIsLoading(true);

            const payload = {
                CaseID: caseId,
                Deadline: deadline,
                MessageID: messageId,
                Email: emailAddress
            };
            const response = await fetch("https://outlook-rbs.it-bed.workers.dev/createNote", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(payload)
            });
            if (response.ok) {
                setIsLoading(false);
                const data = await response.json();
                const text = JSON.parse(data.text)
                if ("Error" in text) {
                    setResult("❌ Error: " + text.Error);
                    return;
                }

                setResult('✅ Note created successfully!');
            } else {
                setIsLoading(false);
                const text = await response.text();
                setResult("❌ Error: " + text);
            }
        } catch (error) {
            setIsLoading(false);
            setResult("❌ Error: " + error.message);
        }
    };
    return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "flex-start",
        // minHeight: "70vh",
        fontFamily: "Roboto, 'Segoe UI', sans-serif",
        backgroundColor: "transparent",
        margin: 0,
        padding: "0 10px 10px 10px",
        color: colors.textColor
      }}
    > 
      <style>{keyframes}</style>

      {/* Form Section - Now responsive and constrained by maxWidth */}
      <div
        style={{
          width: "100%", // Fluid width up to maxWidth
          maxWidth: "480px", // Maximum width on large screens
          padding: "0 30px 30px 30px",
        }}
      >
        <h2 style={{ 
          textAlign: "center", 
          marginBottom: "30px", 
          marginTop: "0", 
          color: colors.textColor
          }}>
          Add New Note
        </h2>
        
        {/* Input Group 1: CaseID */}
        <div style={{ marginBottom: "20px" }}>
          <label
            style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: "600",
              color: colors.labelColor,
              fontSize: "14px",
            }}
          >
            Case ID
          </label>
          <input
            type="text"
            value={caseId}
            onChange={(e) => setCaseId(e.target.value)}
            placeholder="e.g., T12345"
            style={{
              width: "100%", 
              padding: "12px 12px",
              borderRadius: "6px",
              border: `1px solid ${colors.borderColor}`,
              boxSizing: "border-box", 
              fontSize: "14px",
              outline: "none",
              color: colors.inputColor,
              backgroundColor: colors.inputBg,
            }}
            onFocus={(e) => e.target.style.borderColor = colors.primaryColor}
            onBlur={(e) => e.target.style.borderColor = colors.borderColor}
          />
        </div>

        {/* Input Group 2: Deadline */}
        <div style={{ marginBottom: "30px" }}>
          <label
            style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: "600",
              color: colors.labelColor,
              fontSize: "14px",
            }}
          >
            Deadline
          </label>
          <input
            type="date"
            value={deadline}
            onChange={(e) => setDeadline(e.target.value)}
            style={{
              width: "100%", 
              padding: "12px 12px",
              borderRadius: "6px",
              border: `1px solid ${colors.borderColor}`,
              boxSizing: "border-box",
              fontSize: "14px",
              outline: "none",
              color: colors.inputColor,
              backgroundColor: colors.inputBg, 
              // colorScheme: isDark ? 'dark' : 'light',
            }}
            onFocus={(e) => e.target.style.borderColor = colors.primaryColor}
            onBlur={(e) => e.target.style.borderColor = colors.borderColor}
          />
        </div>
        
        {/* Action Button */}
        <div style={{ textAlign: "center", marginBottom: "20px" }}>
          <button
            onClick={addNote}
            disabled={isLoading}
            style={{
              width: "100%",
              padding: "12px 20px",
              borderRadius: "6px",
              border: "none",
              backgroundColor: isLoading ? colors.primaryColorHover : (isHovered ? colors.primaryColorHover : colors.primaryColor),
              color: "#fff",
              fontWeight: "bold",
              fontSize: "16px",
              cursor: isLoading ? "not-allowed" : "pointer",
              transition: "background-color 0.2s ease, transform 0.1s ease",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
            onMouseEnter={() => setIsHovered(true)}
            onMouseLeave={() => setIsHovered(false)}
            onMouseDown={(e) => e.currentTarget.style.transform = "scale(0.98)"}
            onMouseUp={(e) => e.currentTarget.style.transform = "scale(1)"}
          >
            {isLoading && <span style={spinnerStyle}></span>}
            {isLoading ? 'Saving...' : 'Add Note'}
          </button>
        </div>
        <div style={{ textAlign: "center", fontSize: "14px", color: result.startsWith('❌') ? colors.resultErrorColor : colors.resultSuccessColor, minHeight: '20px' }}>
          <span>{result}</span>
        </div>
      </div>
    </div>
  );

}