import * as React from "react";
import { useState, useEffect } from "react";
import PropTypes from "prop-types";
import Header from "./Header";
import AddNoteForm from "./AddNoteForm";
import { ThemeContext } from "./ThemeContext";
import AttachmentsList from "./AttachmentsList";

function isDarkTheme() {
  return typeof window !== "undefined" && window.matchMedia("(prefers-color-scheme: dark)").matches;
}

const App = (props) => {
  const { title } = props;
  const [isDark, setIsDark] = useState(isDarkTheme());
  const [attachments, setAttachments] = useState([]);

  useEffect(() => {
    const media = window.matchMedia("(prefers-color-scheme: dark)");
    const handleChange = (e) => {
      setIsDark(e.matches);
      console.log("Theme changed to: ", e.matches ? "dark" : "light");
    };
    media.addEventListener("change", handleChange);
    return () => media.removeEventListener("change", handleChange);
  }, []);

  useEffect(() => {
    const loadAttachments = () => {
      const item = Office.context.mailbox.item;
      if (!item) {
          setAttachments([]);
          return;
      }

      // Some items might not have attachments
      if (!item.attachments) {
          setAttachments([]);
          return;
      }

      // Map Office attachments to your format
      const mappedAttachments = item.attachments.filter(att => !att.isInline).map((att, index) => ({
        id: att.id || index,
        name: att.name,
        size: att.size || 0
      }));
      console.log("Mapped attachments:", mappedAttachments);
      setAttachments(mappedAttachments);
    };

    loadAttachments();

    // Listen for email changes
    const handler = () => loadAttachments();
    Office.context.mailbox.addHandlerAsync(
      Office.EventType.ItemChanged,
      handler
    );

    return () => Office.context.mailbox.removeHandlerAsync(
      Office.EventType.ItemChanged,
      handler
    );
  }, []);

  const colors = {
    primaryColor: "#1070e0",
    primaryColorHover: "#0d5fb5",
    backgroundColor: isDark ? "#1f1f1f" : "#f5f5f5",
    cardBackground: isDark ? "#2d2d2d" : "#fff",
    textColor: isDark ? "#f0f0f0" : "#333",
    labelColor: isDark ? "#ccc" : "#555",
    borderColor: isDark ? "#444" : "#d1d9e6",
    boxShadow: isDark ? "0 6px 20px rgba(0,0,0,0.3)" : "0 6px 20px rgba(0,0,0,0.05)",
    inputColor: isDark ? "#e0e0e0" : "#333",
    inputBg: isDark ? "#3c3c3c" : "#fff",
    resultErrorColor: isDark ? '#ff7f7f' : '#d32f2f',
    resultSuccessColor: isDark ? '#90ee90' : '#2e7d32',
    warningColor: isDark ? '#ffeb3b' : '#ff9800',
    warningBg: isDark ? '#4c3f00' : '#fff3e0',
    listBg: isDark ? "#242424" : "#f9f9f9",
    listBorder: isDark ? "#333" : "#eee",
    spinnerBorder: isDark ? "#555" : "#f3f3f3",
    spinnerBorderTop: "#fff"
  }

  return (
    <ThemeContext.Provider value={{ isDark, colors}}>
      <div style={{ backgroundColor: "transparent", minHeight: "100vh" }}>
        <Header logo="assets/logo-filled.png" title={title} message="Richards Law" />
        <AddNoteForm />
        {/* <AttachmentsList attachments={attachments} /> */}
      </div>
    </ThemeContext.Provider>  
  );
};

App.propTypes = {
  title: PropTypes.string,
};

export default App;
